body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Container {
    display: flex;
    align-items: center;
  }
  
  table.Scorecard {
    margin: 0 auto;
    width:80%;
    font-size:12px;
    border:1px solid;
    text-align: center;
    table-layout: fixed;
    margin-bottom: 40px;
  }
  
  table.Scorecard th, tr, td {
    padding: 0;
    vertical-align: middle;
    font-weight: bold;
  }
  
  table.Scorecard th {
    border-bottom:1px solid;
    background-color:#D3D3D3;
    height:30px;
  }
  
  table.Scorecard th:not(:last-child) {
    border-right:1px solid;
  }
  
  table.Scorecard td {
    height:30px;
    background: #F0F8FF;
    border-bottom: 1px solid;
  }
  
  table.Scorecard tr td:not(:last-child) {
    border-right:1px solid;
  }
  
  table.Scorecard tr:nth-child(2) td:nth-child(even) {
    border-bottom:1px solid;
  }
  
  table.Scorecard tr:nth-child(2) td:last-child {
    border-bottom:1px solid;
  }
  
  table.Scorecard #r21 {
    border-bottom: 1px solid;
  }
  
  table.Scorecard #total-score {
    background-color: #E6E6FA;
    border-bottom: none;
  }
  
.Container {
    display: flex;
    flex-direction: column;
  }
  
  .Restart {
    width: 100px;
    border-radius: 15px;
    height: 35px;
    margin-top: 40px;
    margin-bottom: 20px;
    color: red;
    border-color: red;
    background-color: transparent;
  }
  
  .Restart:hover {
    background-color: red;
    color: white;
    opacity: 0.9;
  }
  
  button {
    width: auto;
    height: 30px;
    margin: 10px;
    background-color: rgb(81, 81, 234);
    color: white;
    font-size: 12px;
  }
  
  button:hover {
    opacity: 0.7;
    cursor: pointer;
  }
  .image_style{
    width:50px;
    border-radius: 50%;
  }
  
.Game {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .Game-over-text {
    text-align: center;
    margin-top: 10px;
  }
  
.header_style{
    background-color: blue;
    width: 100%
}
.title_style{
    text-align: center;
    font-size: 20px;
    color: #fff;
}
